// ejected using 'npx eject-keycloak-page'
import {useState} from "react";
import {clsx} from "keycloakify/tools/clsx";
import {UserProfileFormFields} from "./shared/UserProfileFormFields";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Button} from "../../components/ui/button";
import {useTitle} from "react-use";
import {Checkbox} from "../../components/ui/checkbox";

export default function Register(props: PageProps<Extract<KcContext, {
    pageId: "register.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {url, messagesPerField, recaptchaRequired, recaptchaSiteKey, termsAcceptanceRequired} = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(`${msgStr("doRegister")} - ${kcContext.realm.displayName}`)
    const [isFormSubmittable, setIsFormSubmittable] = useState(false);

    return (
        <Template
            {...{kcContext, i18n, doUseDefaultCss, classes}}
            displayMessage={messagesPerField.exists("global")}
            displayRequiredFields={true}
            headerNode={msg("doRegister")}
        >
            <div className="lg:p-8 lg:pb-4 flex gap-4">
                <div className={`mx-auto flex w-full flex-col justify-center sm:w-[350px] `}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    <form id="kc-register-form" className={"grid gap-4 items-center"} action={url.registrationAction}
                          method="post">
                        <UserProfileFormFields
                            //@ts-ignore
                            kcContext={kcContext}
                            onIsFormSubmittableValueChange={setIsFormSubmittable}
                            i18n={i18n}
                            getClassName={getClassName}
                        />
                        {termsAcceptanceRequired &&
                            <div className="flex items-center space-x-2">
                                <Checkbox id="termsAccepted" name="termsAccepted"/>
                                <label htmlFor="termsAccepted"
                                       className={"text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                                       }>{msg("acceptTerms")}</label>

                            </div>
                        }
                        {recaptchaRequired && (
                            <div className="form-group">
                                <div className={getClassName("kcInputWrapperClass")}>
                                    <div className="g-recaptcha" data-size="compact" data-sitekey={recaptchaSiteKey}/>
                                </div>
                            </div>
                        )}
                        <div className={"mt-3"}>
                            <div id="kc-form-buttons" className={getClassName("kcFormButtonsClass")}>
                                <Button
                                    className={"w-full"}
                                    type="submit"
                                    disabled={!isFormSubmittable}
                                >
                                    {msgStr("doRegister")}
                                </Button>
                            </div>
                        </div>
                    </form>
                    <div className={"text-sm mt-3 hover:underline text-slate-500"}>
                            <span>
                                <a href={url.loginUrl}>{msg("backToLogin")}</a>
                            </span>
                    </div>
                </div>
            </div>
        </Template>
    );
}
